@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  font-size: 62.5%;
}

* {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  object-fit: cover;
}

.container {
    width: 120rem;
    margin: 0 auto;
}



.cart{display:flex;align-items:center}.cart__counter{position:relative;left:-2rem;font-size:2.4rem;background-color:#ffffff;padding:0.2rem 0.7rem;border-radius:50%;border:0.1rem dashed lightgray}.cart__img{width:4.5rem;height:4.5rem}

.header{background-color:#F5F5F5;padding:1.6rem 0}.header__logo{font-weight:bold;color:#65C5B4;text-transform:uppercase;font-size:2rem;line-height:2.4rem;text-shadow:0 0.5rem 0.7rem lightgray}.header__wrapper{display:flex;justify-content:space-between;align-items:center}

.product{display:flex;flex-direction:column}.product__img-wrap{margin-bottom:1rem;height:65%;overflow:hidden;border-radius:3rem}.product__name{font-size:2rem;line-height:2.8rem}.product__price{font-size:1.6rem;line-height:2rem;margin-bottom:1rem}.product__button{font-size:1.4rem;line-height:1.8rem;padding:0.8rem;text-transform:uppercase;background-color:transparent;font-weight:bold;width:80%;margin:0 auto;border-radius:0.5rem}.product__button:hover,.product__button:focus{background-color:lightgray;outline:none}

.catalog{list-style:none;display:flex;flex-wrap:wrap;justify-content:space-between;padding-top:4rem}.catalog__item{width:30%;margin-bottom:4rem}

